import React, {Component} from 'react';
import { Link} from 'react-router-dom';
import {connect} from 'react-redux';
import {SlideUpDown} from "../../../services/script"
import LogoImage from "../headers/common/logo"

class FooterFour extends Component {
    
    constructor (props) {
        super (props)

    }
    componentDidMount(){
        var contentwidth = window.innerWidth;
        if ((contentwidth) < 750) {
            SlideUpDown('slide-up');
        } else {
            var elems = document.querySelectorAll(".footer-title");
            [].forEach.call(elems, function(elemt) {
                let el = elemt.nextElementSibling;
                el.style = "display: block";
            });
        }
    }


    render () {
        const{socialLinks,country} = this.props;
        return (
            <footer class="">
                <div class="white-layout">
                    <div class="container">
                        <section class="small-section">
                            <div class="row">
                       
                            </div>
                        </section>
                    </div>
                </div>
                <section class="section-b-space darken-layout">
                    <div class="container">
                        <div class="row footer-theme partition-f">
                            <div class="col-lg-3 col-md-6">
                                <div class="footer-title footer-mobile-title">
                                    <h4 class="footer-header" >about</h4>
                                </div>
                                <div class="footer-contant">
                                    <div class="footer-logo d-flex justify-content-center">
                                        <LogoImage logo={this.props.logoName} />
                                    </div>
                                </div>
                            </div>

                            <div class="col-lg-2 col-md-6">
                                <div class="row">
                                    <div class="sub-title col-lg-12 col-md-6">
                                        <div class="footer-title my-md-2 Active">
                                            <h4 class="footer-header align-footer-headers"  style={{
                                                color: '#fff',
                                              
                                                fontSize: '12px',
                                                fontWeight:'700',
                                                margin:'0px'
                                            }}>Connect With us</h4>
                                        </div>
                                        <div class="footer-contant">
                                            <div class="contact-list">
                                                <div class="row align-footers" >
                                                    {socialLinks[country] ? 
                                                        socialLinks[country].map(e=>{
                                                            return  <div >
                                                                        <a href={e.Link} target="_blank">
                                                                            <img class="img-fluid m-1 social-media" src={`https://portal.coolandcool.pk/${e.Icon}` }  alt=""/>
                                                                        </a>
                                                                    </div>
                                                        })
                                                    :
                                                    ''
                                                    }
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="row mt-2 ">
                                    <div class="sub-title col-lg-12 col-md-6">
                                        <div class="footer-title my-md-2 ">
                                            <h4 class="footer-header align-footer-headers"  style={{
                                                color: '#fff',
                                             
                                                fontSize: '12px',
                                                fontWeight:'700',
                                                margin:'0px',
                                            
                                            }}>Payment Methods</h4>
                                        </div>
                                        <div class="footer-contant">
                                        <div class="row align-footers">
                                            <div  >
                                            <img class=" img-fluid payment-media" src="https://backupimages.blob.core.windows.net/watsons/content/media/images/mastercard.svg" alt=""/>
                                            </div>
                                            <div  >
                                            <img class=" img-fluid payment-media"  src="https://backupimages.blob.core.windows.net/watsons/content/media/images/visa.svg" alt=""/>
                                            </div>
                                            <div  >
                                            <img class=" img-fluid payment-media"  src="https://backupimages.blob.core.windows.net/watsons/content/media/images/cod.svg"alt=""/>
                                            </div>
                                        </div>
                                        </div>
                                    </div>
                                </div>
                            </div>



                            <div class="col-lg-2 col-md-6">
                                <div class="row">
                                    <div class="sub-title col-lg-12 col-md-6">
                                        <div class="footer-title my-md-2 Active">
                                            <h4 class="footer-header align-footer-headers"  style={{
                                                color: '#fff',
                                          
                                                fontSize: '12px',
                                                fontWeight:'700',
                                                margin:'0px'
                                            }}>download our app</h4>
                                        </div>
                                        <div class="footer-contant">
                                            <div class="contact-list">
                                                <div class="row align-footers" >
                                                    <div>
                                                        <a href="https://play.google.com/store/apps/details?id=com.sg.coolapp">
                                                            <img src={`${process.env.PUBLIC_URL}/assets/images/google-play.svg`} class="img-fluid p-2" alt=""/>
                                                        </a>
                                                    </div>
                                                    <div>
                                                        <a href="https://apps.apple.com/us/app/cool-cool/id1493928738">
                                                            <img src={`${process.env.PUBLIC_URL}/assets/images/appstore.svg`} class="img-fluid p-2" alt=""/>
                                                        </a>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>





                         
              
                            <div class="col-lg-2 col-md-6">
                                <div class="sub-title">
                                    <div class="footer-title my-md-1 slide-up">
                                        <h4 class="footer-header"  style={{
                                                color: '#fff',
                                              
                                                fontSize: '12px',
                                                fontWeight:'700',
                                                margin:'0px'
                                        }}>About Us</h4>
                                    </div>
                                    <div class="footer-contant">
                                        <ul class="contact-list" style={{marginTop:'10px'}}>
                                            <li class="footer-header" style={{ fontSize:'12px',display:'block',padding:'0px 0px'}}>
                                            <Link to={`/about-us`}>
                                               About Us
                                            </Link>
                                            </li>
                                            <li class="footer-header" style={{ fontSize:'12px',display:'block',padding:'0px 0px'}}>
                                            <Link to={`/privacy-policy`}>
                                              Privacy Policy
                                            </Link>
                                            </li>
                                       
                                            <li class="footer-header" style={{ fontSize:'12px',display:'block',padding:'0px 0px'}}>
                                            <Link to={`/terms-conditions`}>
                                                 Terms and Conditions
                                            </Link>
                                            </li>
                                      
                                            
                                        </ul>
                                    </div>
                                </div>
                            </div>

                            <div class="col-lg-2 col-md-6">
                                <div class="sub-title">
                                    <div class="footer-title my-md-1 slide-up">
                                        <h4 class="footer-header"  style={{
                                                color: '#fff',
                                               
                                                fontSize: '12px',
                                                fontWeight:'700',
                                                margin:'0px'
                                        }}>HELP</h4>
                                    </div>
                                    <div class="footer-contant">
                                    
                                        <ul class="contact-list">
                                       
                                                <li class="footer-header" style={{ fontSize:'12px',display:'block',padding:'0px 0px'}}>
                                                <Link to={`/delivery-terms`}>
                                                    Delivery Infromation
                                                </Link>
                                                </li>
                                         
                                            <li class="footer-header" style={{ fontSize:'12px',display:'block',padding:'0px 0px'}}>
                                            <Link to={`/faq`}>
                                            FAQ`s
                                            </Link>
                                            </li>
                                            <li class="footer-header" style={{ fontSize:'12px',display:'block',padding:'0px 0px'}}>
                                            <Link to={`/contact`}>
                                             Contact Us
                                             </Link>
                                            </li>
                                     
                                        </ul>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                </section>
                <div class="sub-footer dark-subfooter footer-bar">
                    <div class="container">
                        <div class="row">
                            <div class="col-xl-6 col-md-6 col-sm-12">
                                <div class="footer-end">
                                    <p><i class="fa fa-copyright" aria-hidden="true"></i> Copyrighted by Wovista. All Rights Reserved. </p>
                                </div>
                            </div>
                      
                        </div>
                    </div>
                </div>
            </footer>
        )
    }
}


const mapStateToProps = (state) => ({
    socialLinks: state.data.socialLinks,
    country: state.filters.country

})


export default connect(mapStateToProps)(FooterFour);